import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import type { ConfirmationResult } from 'firebase/auth';
import {
  sendVerificationCode,
  verifyCode,
  validatePhoneNumber,
  clearRecaptcha
} from '../../services/phoneAuth';

export default function PhoneLoginPage() {
  const navigate = useNavigate();
  const [step, setStep] = useState<'phone' | 'verify'>('phone');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [loading, setLoading] = useState(false);
  const [confirmationResult, setConfirmationResult] = useState<ConfirmationResult | null>(null);
  const [timer, setTimer] = useState(180); // 3
  const [canResend, setCanResend] = useState(false);

  // 
  useEffect(() => {
    if (step === 'verify' && timer > 0) {
      const interval = setInterval(() => {
        setTimer((prev) => {
          if (prev <= 1) {
            setCanResend(true);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);

      return () => clearInterval(interval);
    }
  }, [step, timer]);

  //    reCAPTCHA 
  useEffect(() => {
    return () => {
      clearRecaptcha();
    };
  }, []);

  //   ( )
  const formatPhoneInput = (value: string) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 7) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7, 11)}`;
  };

  // SMS 
  const handleSendCode = async (e: React.FormEvent) => {
    e.preventDefault();

    //  
    const validation = validatePhoneNumber(phoneNumber);
    if (!validation.valid) {
      alert(validation.error);
      return;
    }

    setLoading(true);

    try {
      const result = await sendVerificationCode(phoneNumber);
      setConfirmationResult(result);
      setStep('verify');
      setTimer(180);
      setCanResend(false);
      alert(' .');
    } catch (error: any) {
      alert(error.message);
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleResendCode = async () => {
    if (!canResend) return;

    setLoading(true);
    try {
      const result = await sendVerificationCode(phoneNumber);
      setConfirmationResult(result);
      setTimer(180);
      setCanResend(false);
      alert(' .');
    } catch (error: any) {
      alert(error.message);
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleVerifyCode = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!confirmationResult) {
      alert('  .');
      return;
    }

    if (verificationCode.length !== 6) {
      alert(' 6 .');
      return;
    }

    setLoading(true);

    try {
      //    (  )
      const { user, error, isNewUser, userData } = await verifyCode(confirmationResult, verificationCode, phoneNumber);

      if (error) {
        alert(error.message);
        return;
      }

      if (isNewUser) {
        //   -   
        alert(' .  .');
        // Firebase UID  state 
        navigate('/signup', {
          state: {
            firebaseUid: user?.uid,
            phoneNumber: phoneNumber
          }
        });
      } else {
        //   -  
        alert(' !');
        console.log(' :', userData);

        //     
        localStorage.setItem('user', JSON.stringify(userData));

        navigate('/');
      }
    } catch (error: any) {
      alert(' .');
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleEditPhone = () => {
    setStep('phone');
    setVerificationCode('');
    setTimer(180);
    setCanResend(false);
  };

  //   (mm:ss)
  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-cyan-50 via-sky-50 to-blue-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/*  */}
        <div className="text-center mb-8">
          <div className="flex items-center justify-center mb-4">
            <div className="relative">
              <div className="w-16 h-16 bg-gradient-to-br from-cyan-500 via-sky-500 to-blue-600 rounded-2xl flex items-center justify-center shadow-2xl">
                <div className="w-12 h-12 flex items-center justify-center">
                  <i className="ri-heart-fill text-white text-3xl"></i>
                </div>
              </div>
              <div className="absolute -top-2 -right-2 w-5 h-5 bg-yellow-400 rounded-full border-2 border-white"></div>
            </div>
          </div>
          <h1 className="text-4xl font-bold bg-gradient-to-r from-cyan-600 to-blue-600 bg-clip-text text-transparent mb-2">
            Attract
          </h1>
          <p className="text-gray-600">   </p>
        </div>

        {/*   */}
        <div className="bg-white rounded-3xl shadow-xl p-8">
          <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">
            {step === 'phone' ? '  ' : ' '}
          </h2>

          {/*   */}
          {step === 'phone' && (
            <form onSubmit={handleSendCode} className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <input
                  type="tel"
                  value={phoneNumber}
                  onChange={(e) => setPhoneNumber(formatPhoneInput(e.target.value))}
                  className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500 text-lg"
                  placeholder="010-1234-5678"
                  maxLength={13}
                  autoFocus
                />
                <p className="mt-2 text-xs text-gray-500">
                     
                </p>
              </div>

              <button
                type="submit"
                disabled={loading || phoneNumber.length < 13}
                className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 text-white py-4 rounded-xl font-medium hover:from-cyan-600 hover:to-blue-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? ' ...' : ' '}
              </button>
            </form>
          )}

          {/*   */}
          {step === 'verify' && (
            <form onSubmit={handleVerifyCode} className="space-y-6">
              <div>
                <div className="flex items-center justify-between mb-2">
                  <label className="block text-sm font-medium text-gray-700">
                    
                  </label>
                  <button
                    type="button"
                    onClick={handleEditPhone}
                    className="text-xs text-cyan-500 hover:text-cyan-600"
                  >
                     
                  </button>
                </div>
                <p className="text-sm text-gray-600 mb-3">
                  {phoneNumber}   
                </p>
                <input
                  type="text"
                  inputMode="numeric"
                  pattern="[0-9]*"
                  value={verificationCode}
                  onChange={(e) => setVerificationCode(e.target.value.replace(/[^\d]/g, '').slice(0, 6))}
                  className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500 text-lg text-center tracking-widest"
                  placeholder="000000"
                  maxLength={6}
                  autoFocus
                />
                <div className="mt-2 flex items-center justify-between">
                  <p className={`text-sm ${timer > 0 ? 'text-cyan-500' : 'text-red-500'}`}>
                    {timer > 0 ? ` : ${formatTime(timer)}` : ' '}
                  </p>
                  <button
                    type="button"
                    onClick={handleResendCode}
                    disabled={!canResend || loading}
                    className="text-sm text-gray-600 hover:text-cyan-500 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    
                  </button>
                </div>
              </div>

              <button
                type="submit"
                disabled={loading || verificationCode.length !== 6}
                className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 text-white py-4 rounded-xl font-medium hover:from-cyan-600 hover:to-blue-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? ' ...' : ''}
              </button>
            </form>
          )}

          {/* reCAPTCHA  (invisible) */}
          <div id="recaptcha-container"></div>
        </div>

        {/*   */}
        <div className="text-center mt-6 text-sm text-gray-600">
          <p>
             ?{' '}
            <button className="text-cyan-500 hover:text-cyan-600">
              
            </button>
          </p>
        </div>
      </div>
    </div>
  );
}
