import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../../lib/supabase';

export default function QuickSignupPage() {
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: '',
    phoneNumber: '',
    age: '',
    gender: '',
    location: '',
    bio: '',
    profileImage: ''
  });
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //   ( )
  const formatPhoneInput = (value: string) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 7) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7, 11)}`;
  };

  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const formatted = formatPhoneInput(e.target.value);
    setFormData(prev => ({
      ...prev,
      phoneNumber: formatted
    }));
  };

  //   
  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    //    (5MB )
    if (file.size > 5 * 1024 * 1024) {
      alert('  5MB  .');
      return;
    }

    //    
    if (!file.type.startsWith('image/')) {
      alert('   .');
      return;
    }

    setSelectedFile(file);

    //  
    const reader = new FileReader();
    reader.onloadend = () => {
      setPreviewUrl(reader.result as string);
    };
    reader.readAsDataURL(file);
  };

  //  
  const uploadImage = async (): Promise<string | null> => {
    if (!selectedFile) return null;

    setUploading(true);

    try {
      //   ( + )
      const fileExt = selectedFile.name.split('.').pop();
      const fileName = `${Date.now()}_${Math.random().toString(36).substring(7)}.${fileExt}`;
      const filePath = `avatars/${fileName}`;

      // Supabase Storage 
      const { data, error } = await supabase.storage
        .from('avatars')
        .upload(filePath, selectedFile, {
          cacheControl: '3600',
          upsert: false
        });

      if (error) {
        console.error('  :', error);
        throw error;
      }

      //  URL 
      const { data: { publicUrl } } = supabase.storage
        .from('avatars')
        .getPublicUrl(filePath);

      console.log('  :', publicUrl);
      return publicUrl;
    } catch (error) {
      console.error('  :', error);
      alert('  .');
      return null;
    } finally {
      setUploading(false);
    }
  };

  //  
  const handleRemoveImage = () => {
    setSelectedFile(null);
    setPreviewUrl('');
    setFormData(prev => ({
      ...prev,
      profileImage: ''
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.name.trim()) {
      alert(' .');
      return;
    }

    if (!formData.phoneNumber || formData.phoneNumber.length < 13) {
      alert('  .');
      return;
    }

    if (!formData.gender) {
      alert(' .');
      return;
    }

    if (!selectedFile && !formData.profileImage) {
      alert('   1  .');
      return;
    }

    setLoading(true);

    try {
      //   (   )
      let imageUrl = formData.profileImage;
      if (selectedFile) {
        const uploadedUrl = await uploadImage();
        if (uploadedUrl) {
          imageUrl = uploadedUrl;
        } else {
          alert('  .  .');
          setLoading(false);
          return;
        }
      }

      if (!imageUrl) {
        alert('  .');
        setLoading(false);
        return;
      }

      //   
      const cleanedPhone = formData.phoneNumber.replace(/[^\d]/g, '');
      const { data: existingUser } = await supabase
        .from('users')
        .select('id')
        .eq('phone_number', cleanedPhone)
        .single();

      if (existingUser) {
        alert('  .');
        setLoading(false);
        return;
      }

      //    (photos )
      const photosArray = imageUrl ? [imageUrl] : [];

      //    (firebase_uid )
      const { data, error } = await supabase
        .from('users')
        .insert({
          phone_number: cleanedPhone,
          name: formData.name.trim(),
          age: formData.age ? parseInt(formData.age) : null,
          gender: formData.gender,
          location: formData.location.trim() || null,
          bio: formData.bio.trim() || null,
          photos: photosArray,
          created_at: new Date().toISOString()
        })
        .select()
        .single();

      if (error) {
        console.error(' :', error);
        alert(' .  .');
        setLoading(false);
        return;
      }

      console.log('   :', data);
      alert(` !\n: ${formData.name}\n: ${formData.phoneNumber}`);

      //  
      setFormData({
        name: '',
        phoneNumber: '',
        age: '',
        gender: '',
        location: '',
        bio: '',
        profileImage: ''
      });
      setSelectedFile(null);
      setPreviewUrl('');
    } catch (error) {
      console.error(' :', error);
      alert('   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-pink-50 to-rose-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/*  */}
        <div className="text-center mb-8">
          <div className="flex items-center justify-center mb-4">
            <div className="relative">
              <div className="w-16 h-16 bg-gradient-to-br from-purple-500 via-pink-500 to-rose-600 rounded-2xl flex items-center justify-center shadow-2xl">
                <div className="w-12 h-12 flex items-center justify-center">
                  <i className="ri-user-add-fill text-white text-3xl"></i>
                </div>
              </div>
              <div className="absolute -top-2 -right-2 w-5 h-5 bg-yellow-400 rounded-full border-2 border-white"></div>
            </div>
          </div>
          <h1 className="text-4xl font-bold bg-gradient-to-r from-purple-600 to-rose-600 bg-clip-text text-transparent mb-2">
             
          </h1>
          <p className="text-gray-600">   </p>
          <p className="text-xs text-purple-500 mt-2">    ()</p>
        </div>

        {/*   */}
        <div className="bg-white rounded-3xl shadow-xl p-8">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-gray-800">
                
            </h2>
            <button
              type="button"
              onClick={() => navigate('/login')}
              className="text-sm text-gray-500 hover:text-gray-700"
            >
               
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-5">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500"
                placeholder=" "
                maxLength={20}
                required
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="tel"
                name="phoneNumber"
                value={formData.phoneNumber}
                onChange={handlePhoneChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500"
                placeholder="010-1234-5678"
                maxLength={13}
                required
              />
              <p className="text-xs text-gray-500 mt-1">
                   
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="number"
                name="age"
                value={formData.age}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500"
                placeholder=" "
                min="18"
                max="100"
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <div className="grid grid-cols-2 gap-3">
                <button
                  type="button"
                  onClick={() => setFormData(prev => ({ ...prev, gender: 'male' }))}
                  className={`py-3 rounded-xl font-medium transition-all ${
                    formData.gender === 'male'
                      ? 'bg-purple-500 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => setFormData(prev => ({ ...prev, gender: 'female' }))}
                  className={`py-3 rounded-xl font-medium transition-all ${
                    formData.gender === 'female'
                      ? 'bg-purple-500 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  
                </button>
              </div>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="text"
                name="location"
                value={formData.location}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500"
                placeholder=": , "
                maxLength={50}
              />
            </div>

            {/*    */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                  <span className="text-red-500">*</span>
              </label>

              {/*  */}
              {previewUrl && (
                <div className="mb-3 relative">
                  <img
                    src={previewUrl}
                    alt=""
                    className="w-32 h-32 object-cover rounded-xl mx-auto border-2 border-purple-200"
                  />
                  <button
                    type="button"
                    onClick={handleRemoveImage}
                    className="absolute top-0 right-1/2 translate-x-16 -translate-y-2 bg-red-500 text-white rounded-full w-6 h-6 flex items-center justify-center hover:bg-red-600 transition-all"
                  >
                    
                  </button>
                </div>
              )}

              {/*    */}
              <div className="flex gap-2">
                <label className="flex-1 bg-purple-50 border-2 border-dashed border-purple-300 rounded-xl px-4 py-3 text-center cursor-pointer hover:bg-purple-100 transition-all">
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleFileSelect}
                    className="hidden"
                  />
                  <i className="ri-image-add-line text-purple-500 mr-2"></i>
                  <span className="text-sm text-purple-600">
                    {selectedFile ? selectedFile.name : ' '}
                  </span>
                </label>
              </div>

              {/* URL  ( ) */}
              <div className="mt-2">
                <input
                  type="url"
                  name="profileImage"
                  value={formData.profileImage}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500 text-sm"
                  placeholder="  URL "
                  disabled={!!selectedFile}
                />
              </div>

              <p className="text-xs text-gray-500 mt-1">
                   URL  ( 5MB)
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500 resize-none"
                placeholder=" "
                rows={4}
                maxLength={200}
              />
              <p className="text-xs text-gray-500 mt-1 text-right">
                {formData.bio.length}/200
              </p>
            </div>

            {/*   */}
            <button
              type="submit"
              disabled={loading || uploading || !formData.name.trim() || !formData.phoneNumber || !formData.gender}
              className="w-full bg-gradient-to-r from-purple-500 to-rose-500 text-white py-4 rounded-xl font-medium hover:from-purple-600 hover:to-rose-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {uploading ? '  ...' : loading ? ' ...' : '  '}
            </button>
          </form>

          {/*   */}
          <div className="mt-6 p-4 bg-purple-50 rounded-xl border border-purple-200">
            <p className="text-xs text-purple-700">
              <i className="ri-information-fill mr-1"></i>
                    .     .
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
