import { createContext, useContext, useEffect, useState } from 'react';
import type { ReactNode } from 'react';
import { supabase } from '../lib/supabase';

interface AuthUser {
  id: string;
  phone_number: string;
  name: string;
  profile_image?: string;
}

interface AuthContextType {
  user: AuthUser | null;
  loading: boolean;
  signInPhone: (phoneNumber: string, password: string) => Promise<{ error: any }>;
  signUpPhone: (phoneNumber: string, password: string, name: string) => Promise<{ error: any }>;
  signOut: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

//    ( )
async function hashPassword(password: string): Promise<string> {
  const encoder = new TextEncoder();
  const data = encoder.encode(password);
  const hashBuffer = await crypto.subtle.digest('SHA-256', data);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  return hashHex;
}

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<AuthUser | null>(null);
  const [loading, setLoading] = useState(true);

  //    localStorage   
  useEffect(() => {
    const storedUser = localStorage.getItem('auth_user');
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser));
      } catch (err) {
        console.error('Failed to parse stored user:', err);
      }
    }
    setLoading(false);
  }, []);

  const signInPhone = async (phoneNumber: string, password: string) => {
    try {
      //   
      const { data: users, error: queryError } = await supabase
        .from('users')
        .select('*')
        .eq('phone_number', phoneNumber)
        .single();

      if (queryError || !users) {
        return { error: { message: '   .' } };
      }

      //  
      const hashedPassword = await hashPassword(password);
      if (users.password_hash !== hashedPassword) {
        return { error: { message: '  .' } };
      }

      //    
      await supabase
        .from('users')
        .update({ last_login: new Date().toISOString() })
        .eq('id', users.id);

      const authUser: AuthUser = {
        id: users.id,
        phone_number: users.phone_number,
        name: users.name,
        profile_image: users.profile_image,
      };

      setUser(authUser);
      localStorage.setItem('auth_user', JSON.stringify(authUser));

      return { error: null };
    } catch (error: any) {
      return { error: { message: error.message } };
    }
  };

  const signUpPhone = async (phoneNumber: string, password: string, name: string) => {
    try {
      //  
      const { data: existingUser } = await supabase
        .from('users')
        .select('id')
        .eq('phone_number', phoneNumber)
        .single();

      if (existingUser) {
        return { error: { message: '  .' } };
      }

      //   
      const hashedPassword = await hashPassword(password);
      const { data: newUser, error: insertError } = await supabase
        .from('users')
        .insert({
          phone_number: phoneNumber,
          password_hash: hashedPassword,
          name: name,
          gender: 'male',
        })
        .select()
        .single();

      if (insertError || !newUser) {
        return { error: insertError || { message: ' .' } };
      }

      const authUser: AuthUser = {
        id: newUser.id,
        phone_number: newUser.phone_number,
        name: newUser.name,
        profile_image: newUser.profile_image,
      };

      setUser(authUser);
      localStorage.setItem('auth_user', JSON.stringify(authUser));

      return { error: null };
    } catch (error: any) {
      return { error: { message: error.message } };
    }
  };

  const signOut = async () => {
    setUser(null);
    localStorage.removeItem('auth_user');
  };

  const value: AuthContextType = {
    user,
    loading,
    signInPhone,
    signUpPhone,
    signOut,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
