import {
  RecaptchaVerifier,
  signInWithPhoneNumber
} from 'firebase/auth';
import type { ConfirmationResult } from 'firebase/auth';
import { firebaseAuth } from '../lib/firebase';
import { supabase } from '../lib/supabase';

// reCAPTCHA verifier 
let recaptchaVerifier: RecaptchaVerifier | null = null;

/**
 * reCAPTCHA 
 * @param containerId - reCAPTCHA  DOM  ID
 */
export const initRecaptcha = (containerId: string = 'recaptcha-container'): RecaptchaVerifier => {
  if (recaptchaVerifier) {
    return recaptchaVerifier;
  }

  recaptchaVerifier = new RecaptchaVerifier(firebaseAuth, containerId, {
    size: 'invisible',
    callback: () => {
      console.log('reCAPTCHA verified');
    },
    'expired-callback': () => {
      console.log('reCAPTCHA expired');
    }
  });

  return recaptchaVerifier;
};

/**
 * SMS  
 * @param phoneNumber -  (: +821012345678)
 * @returns ConfirmationResult (  )
 */
export const sendVerificationCode = async (phoneNumber: string): Promise<ConfirmationResult> => {
  try {
    // +82  
    const formattedPhone = formatPhoneNumber(phoneNumber);

    // reCAPTCHA 
    const appVerifier = initRecaptcha();

    // SMS 
    const confirmationResult = await signInWithPhoneNumber(
      firebaseAuth,
      formattedPhone,
      appVerifier
    );

    console.log('SMS  :', formattedPhone);
    return confirmationResult;
  } catch (error: any) {
    console.error('SMS  :', error);
    throw new Error(getSMSErrorMessage(error.code));
  }
};

/**
 *    / 
 * @param confirmationResult - SMS    
 * @param verificationCode -   
 * @param phoneNumber -  (  )
 * @returns isNewUser:   
 */
export const verifyCode = async (
  confirmationResult: ConfirmationResult,
  verificationCode: string,
  phoneNumber?: string
) => {
  try {
    // Firebase 
    const result = await confirmationResult.confirm(verificationCode);
    const user = result.user;

    console.log('Firebase  :', user.uid);

    // Supabase    (  )
    const { isNewUser, userData } = await checkUserExists(user.uid, phoneNumber);

    return {
      user,
      error: null,
      isNewUser, //   
      userData //   
    };
  } catch (error: any) {
    console.error('  :', error);
    return {
      user: null,
      error: new Error(getVerificationErrorMessage(error.code)),
      isNewUser: false,
      userData: null
    };
  }
};

/**
 * Supabase    
 * firebase_uid  ,    (  )
 */
const checkUserExists = async (firebaseUid: string, phoneNumber?: string) => {
  try {
    console.log(' Supabase  :', {
      firebaseUid,
      phoneNumber,
      supabaseClient: supabase ? ' ' : ' '
    });

    // 1. firebase_uid  
    const { data: existingUser, error: checkError } = await supabase
      .from('users')
      .select('*')
      .eq('firebase_uid', firebaseUid)
      .single();

    console.log(' firebase_uid  :', {
      hasData: !!existingUser,
      error: checkError ? checkError.message : null,
      errorCode: checkError ? checkError.code : null
    });

    if (checkError && checkError.code !== 'PGRST116') {
      throw checkError;
    }

    // firebase_uid   
    if (existingUser) {
      console.log(' firebase_uid   ');
      return { isNewUser: false, userData: existingUser };
    }

    // 2. firebase_uid  ,     ( )
    if (phoneNumber) {
      //  010XXXXXXXX   (DB   )
      // 010-1234-5678  01012345678
      // +821012345678  01012345678
      let cleanedPhone = phoneNumber.replace(/[^\d]/g, ''); //  

      // +82  82  0 
      if (cleanedPhone.startsWith('82')) {
        cleanedPhone = '0' + cleanedPhone.substring(2);
      }

      console.log('  :', phoneNumber);
      console.log('  :', cleanedPhone);

      const { data: ghostUser, error: ghostError } = await supabase
        .from('users')
        .select('*')
        .eq('phone_number', cleanedPhone)
        .is('firebase_uid', null)
        .maybeSingle(); // single  maybeSingle  (0  1 )

      console.log('   :', {
        hasData: !!ghostUser,
        error: ghostError ? ghostError.message : null,
        searchedPhone: cleanedPhone
      });

      if (ghostUser) {
        console.log('   ! firebase_uid  ...');

        //   firebase_uid 
        const { error: updateError } = await supabase
          .from('users')
          .update({ firebase_uid: firebaseUid })
          .eq('id', ghostUser.id);

        if (updateError) {
          console.error('firebase_uid  :', updateError);
        } else {
          console.log(' firebase_uid  ');
          ghostUser.firebase_uid = firebaseUid; //   
        }

        return { isNewUser: false, userData: ghostUser };
      }
    }

    //    
    console.log('  ');
    return { isNewUser: true, userData: null };
  } catch (error) {
    console.error('  :', error);
    return { isNewUser: true, userData: null };
  }
};

/**
 *    (   )
 * @param firebaseUid - Firebase UID
 * @param phoneNumber - 
 * @param userData -   
 */
export const createUserProfile = async (
  firebaseUid: string,
  phoneNumber: string,
  userData: {
    name: string;
    age?: number;
    gender?: string;
    location?: string;
    bio?: string;
    mbti?: string;
    school?: string;
    height?: string;
    body_type?: string;
    style?: string;
    religion?: string;
    smoking?: string;
    drinking?: string;
    interests?: string[];
    photos?: string[];
    password?: string; //  
  }
) => {
  try {
    //     (010XXXXXXXX )
    const cleanedPhone = phoneNumber.replace(/[^\d]/g, '').replace(/^\+82/, '0');

    console.log('  :', cleanedPhone);

    const { data, error} = await supabase
      .from('users')
      .insert({
        firebase_uid: firebaseUid,
        phone_number: cleanedPhone,
        name: userData.name,
        age: userData.age,
        gender: userData.gender,
        location: userData.location,
        bio: userData.bio,
        mbti: userData.mbti,
        school: userData.school,
        height: userData.height,
        body_type: userData.body_type,
        style: userData.style,
        religion: userData.religion,
        smoking: userData.smoking,
        drinking: userData.drinking,
        interests: userData.interests,
        photos: userData.photos,
        password: userData.password || null, //  
        created_at: new Date().toISOString()
      })
      .select()
      .single();

    if (error) {
      console.error('  :', error);
      throw error;
    }

    console.log('Supabase   :', data);
    return { data, error: null };
  } catch (error: any) {
    console.error('createUserProfile :', error);
    return { data: null, error };
  }
};

/**
 *   (  )
 * @param phoneNumber -  
 * @returns +82  
 */
export const formatPhoneNumber = (phoneNumber: string): string => {
  //  
  let cleaned = phoneNumber.replace(/[^\d]/g, '');

  // 0  +82 
  if (cleaned.startsWith('0')) {
    cleaned = '+82' + cleaned.substring(1);
  }

  // +82  
  if (!cleaned.startsWith('+82')) {
    cleaned = '+82' + cleaned;
  }

  return cleaned;
};

/**
 *   
 */
export const validatePhoneNumber = (phoneNumber: string): { valid: boolean; error?: string } => {
  const cleaned = phoneNumber.replace(/[\s-]/g, '');

  //     (010, 011, 016, 017, 018, 019)
  const phoneRegex = /^(0(10|11|16|17|18|19))\d{7,8}$/;

  if (!phoneRegex.test(cleaned)) {
    return {
      valid: false,
      error: '   . (: 010-1234-5678)'
    };
  }

  return { valid: true };
};

/**
 * SMS   
 */
const getSMSErrorMessage = (errorCode: string): string => {
  switch (errorCode) {
    case 'auth/invalid-phone-number':
      return '  .';
    case 'auth/missing-phone-number':
      return ' .';
    case 'auth/quota-exceeded':
      return 'SMS   .    .';
    case 'auth/too-many-requests':
      return '   .    .';
    default:
      return 'SMS  .  .';
  }
};

/**
 *    
 */
const getVerificationErrorMessage = (errorCode: string): string => {
  switch (errorCode) {
    case 'auth/invalid-verification-code':
      return ' .';
    case 'auth/code-expired':
      return ' .  .';
    default:
      return ' .  .';
  }
};

/**
 * reCAPTCHA  
 */
export const clearRecaptcha = () => {
  if (recaptchaVerifier) {
    recaptchaVerifier.clear();
    recaptchaVerifier = null;
  }
};
