import { createClient } from '@supabase/supabase-js';

const supabaseUrl = import.meta.env.VITE_SUPABASE_URL || '';
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY || '';

console.log(' Supabase Configuration Debug:');
console.log('URL:', supabaseUrl ? ` ${supabaseUrl.substring(0, 30)}...` : ' MISSING');
console.log('ANON_KEY:', supabaseAnonKey ? ` ${supabaseAnonKey.substring(0, 30)}...` : ' MISSING');

if (!supabaseUrl || !supabaseAnonKey) {
  console.error(' Supabase configuration is missing! Check your .env file.');
  console.error('Expected variables: VITE_SUPABASE_URL, VITE_SUPABASE_ANON_KEY');
}

export const supabase = createClient(supabaseUrl, supabaseAnonKey);

//   
export const auth = {
  //  
  signInWithEmail: async (email: string, password: string) => {
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password,
    });
    return { data, error };
  },

  // 
  signUp: async (email: string, password: string, name: string) => {
    const { data, error } = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: {
          name,
        },
      },
    });
    return { data, error };
  },

  // 
  signOut: async () => {
    const { error } = await supabase.auth.signOut();
    return { error };
  },

  //   
  getSession: async () => {
    const { data, error } = await supabase.auth.getSession();
    return { data, error };
  },

  //   
  getCurrentUser: async () => {
    const { data: { user }, error } = await supabase.auth.getUser();
    return { user, error };
  },

  //    
  onAuthStateChange: (callback: (event: string, session: any) => void) => {
    return supabase.auth.onAuthStateChange(callback);
  },
};

//   
export const userService = {
  //    ()
  findUserByPhoneNumber: async (phoneNumber: string) => {
    try {
      console.log('   :', phoneNumber);
      
      const { data: users, error } = await supabase
        .from('users')
        .select('*')
        .eq('phone_number', phoneNumber);

      const user = users && users.length > 0 ? users[0] : null;

      console.log(' Supabase :', {
        hasUser: !!user,
        errorMessage: error?.message,
        errorCode: error?.code
      });

      if (error) {
        throw error;
      }

      return { user, error: null };
    } catch (error: any) {
      console.error('   :', error);
      return { user: null, error };
    }
  },

  // Firebase UID   ( )
  findUserByFirebaseUid: async (firebaseUid: string) => {
    try {
      console.log(' Firebase UID  :', firebaseUid);
      
      const { data: users, error } = await supabase
        .from('users')
        .select('*')
        .eq('firebase_uid', firebaseUid);

      const user = users && users.length > 0 ? users[0] : null;

      if (error) {
        throw error;
      }

      return { user, error: null };
    } catch (error: any) {
      console.error(' Firebase UID  :', error);
      return { user: null, error };
    }
  },

  //   
  createUserProfile: async (userData: {
    firebase_uid: string;
    phone_number: string;
    name: string;
    age?: number;
    gender?: string;
    location?: string;
    bio?: string;
  }) => {
    try {
      console.log('    :', userData.phone_number);
      
      const { data: createdUsers, error } = await supabase
        .from('users')
        .insert([userData])
        .select();

      const data = createdUsers && createdUsers.length > 0 ? createdUsers[0] : null;

      if (error) throw error;

      console.log('    :', data);
      return { data, error: null };
    } catch (error: any) {
      console.error('    :', error);
      return { data: null, error };
    }
  },

  //   
  updateUserProfile: async (userId: string, updates: any) => {
    try {
      const { data: updatedUsers, error } = await supabase
        .from('users')
        .update(updates)
        .eq('id', userId)
        .select();

      const data = updatedUsers && updatedUsers.length > 0 ? updatedUsers[0] : null;

      if (error) throw error;

      return { data, error: null };
    } catch (error: any) {
      console.error('   :', error);
      return { data: null, error };
    }
  },
};
