import { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { createUserProfile } from '../../services/phoneAuth';
import { supabase } from '../../lib/supabase';

interface LocationState {
  firebaseUid: string;
  phoneNumber: string;
}

export default function SignupPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const state = location.state as LocationState;

  // state    
  if (!state || !state.firebaseUid || !state.phoneNumber) {
    navigate('/login');
    return null;
  }

  const [formData, setFormData] = useState({
    name: '',
    age: '',
    gender: '',
    location: '',
    bio: '',
    mbti: '',
    school: '',
    height: '',
    bodyType: '',
    style: '',
    religion: '',
    smoking: '',
    drinking: '',
    profileImage: ''
  });
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [interests, setInterests] = useState<string[]>([]);
  const [newInterest, setNewInterest] = useState('');

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //   
  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    //    (5MB )
    if (file.size > 5 * 1024 * 1024) {
      alert('  5MB  .');
      return;
    }

    //    
    if (!file.type.startsWith('image/')) {
      alert('   .');
      return;
    }

    setSelectedFile(file);

    //  
    const reader = new FileReader();
    reader.onloadend = () => {
      setPreviewUrl(reader.result as string);
    };
    reader.readAsDataURL(file);
  };

  //  
  const uploadImage = async (): Promise<string | null> => {
    if (!selectedFile) return null;

    setUploading(true);

    try {
      //   ( + )
      const fileExt = selectedFile.name.split('.').pop();
      const fileName = `${Date.now()}_${Math.random().toString(36).substring(7)}.${fileExt}`;
      const filePath = `avatars/${fileName}`;

      // Supabase Storage 
      const { data, error } = await supabase.storage
        .from('avatars')
        .upload(filePath, selectedFile, {
          cacheControl: '3600',
          upsert: false
        });

      if (error) {
        console.error('  :', error);
        throw error;
      }

      //  URL 
      const { data: { publicUrl } } = supabase.storage
        .from('avatars')
        .getPublicUrl(filePath);

      console.log('  :', publicUrl);
      return publicUrl;
    } catch (error) {
      console.error('  :', error);
      alert('  .');
      return null;
    } finally {
      setUploading(false);
    }
  };

  //  
  const handleRemoveImage = () => {
    setSelectedFile(null);
    setPreviewUrl('');
    setFormData(prev => ({
      ...prev,
      profileImage: ''
    }));
  };

  //  
  const handleAddInterest = () => {
    if (newInterest.trim() && !interests.includes(newInterest.trim())) {
      setInterests([...interests, newInterest.trim()]);
      setNewInterest('');
    }
  };

  //  
  const handleRemoveInterest = (interest: string) => {
    setInterests(interests.filter(item => item !== interest));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.name.trim()) {
      alert(' .');
      return;
    }

    if (!formData.gender) {
      alert(' .');
      return;
    }

    if (!selectedFile && !formData.profileImage) {
      alert('   1  .');
      return;
    }

    setLoading(true);

    try {
      //   (   )
      let imageUrl = formData.profileImage;
      if (selectedFile) {
        const uploadedUrl = await uploadImage();
        if (uploadedUrl) {
          imageUrl = uploadedUrl;
        } else {
          alert('  .  .');
          setLoading(false);
          return;
        }
      }

      if (!imageUrl) {
        alert('  .');
        setLoading(false);
        return;
      }

      //   
      const photosArray = imageUrl ? [imageUrl] : [];

      const { data, error } = await createUserProfile(
        state.firebaseUid,
        state.phoneNumber,
        {
          name: formData.name.trim(),
          age: formData.age ? parseInt(formData.age) : undefined,
          gender: formData.gender,
          location: formData.location.trim() || undefined,
          bio: formData.bio.trim() || undefined,
          mbti: formData.mbti || undefined,
          school: formData.school.trim() || undefined,
          height: formData.height || undefined,
          body_type: formData.bodyType || undefined,
          style: formData.style || undefined,
          religion: formData.religion || undefined,
          smoking: formData.smoking || undefined,
          drinking: formData.drinking || undefined,
          interests: interests.length > 0 ? interests : undefined,
          photos: photosArray
        }
      );

      if (error) {
        alert(' .  .');
        console.error(error);
        return;
      }

      //    localStorage  ( )
      if (data) {
        localStorage.setItem('user', JSON.stringify(data));
        console.log('    :', data);
      }

      alert(' !');
      navigate('/', { replace: true });
    } catch (error) {
      alert('   .');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-cyan-50 via-sky-50 to-blue-50 py-8 px-4">
      <div className="w-full max-w-md mx-auto">
        {/*  */}
        <div className="text-center mb-8">
          <div className="flex items-center justify-center mb-4">
            <div className="relative">
              <div className="w-16 h-16 bg-gradient-to-br from-cyan-500 via-sky-500 to-blue-600 rounded-2xl flex items-center justify-center shadow-2xl">
                <i className="ri-heart-fill text-white text-3xl"></i>
              </div>
              <div className="absolute -top-2 -right-2 w-5 h-5 bg-yellow-400 rounded-full border-2 border-white"></div>
            </div>
          </div>
          <h1 className="text-4xl font-bold bg-gradient-to-r from-cyan-600 to-blue-600 bg-clip-text text-transparent mb-2">
            Attract
          </h1>
          <p className="text-gray-600"> </p>
        </div>

        {/*   */}
        <div className="bg-white rounded-3xl shadow-xl p-8">
          <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">
            
          </h2>

          <form onSubmit={handleSubmit} className="space-y-5">
            {/*    */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                  <span className="text-red-500">*</span>
              </label>

              {/*  */}
              {previewUrl && (
                <div className="mb-3 relative">
                  <img
                    src={previewUrl}
                    alt=""
                    className="w-32 h-32 object-cover rounded-xl mx-auto border-2 border-cyan-200"
                  />
                  <button
                    type="button"
                    onClick={handleRemoveImage}
                    className="absolute top-0 right-1/2 translate-x-16 -translate-y-2 bg-red-500 text-white rounded-full w-6 h-6 flex items-center justify-center hover:bg-red-600 transition-all"
                  >
                    
                  </button>
                </div>
              )}

              {/*    */}
              <div className="flex gap-2">
                <label className="flex-1 bg-cyan-50 border-2 border-dashed border-cyan-300 rounded-xl px-4 py-3 text-center cursor-pointer hover:bg-cyan-100 transition-all">
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleFileSelect}
                    className="hidden"
                  />
                  <i className="ri-image-add-line text-cyan-500 mr-2"></i>
                  <span className="text-sm text-cyan-600">
                    {selectedFile ? selectedFile.name : ' '}
                  </span>
                </label>
              </div>

              {/* URL  ( ) */}
              <div className="mt-2">
                <input
                  type="url"
                  name="profileImage"
                  value={formData.profileImage}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500 text-sm"
                  placeholder="  URL "
                  disabled={!!selectedFile}
                />
              </div>

              <p className="text-xs text-gray-500 mt-1">
                   URL  ( 5MB)
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                placeholder=" "
                maxLength={20}
                required
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="number"
                name="age"
                value={formData.age}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                placeholder=" "
                min="18"
                max="100"
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <div className="grid grid-cols-2 gap-3">
                <button
                  type="button"
                  onClick={() => setFormData(prev => ({ ...prev, gender: 'male' }))}
                  className={`py-3 rounded-xl font-medium transition-all ${
                    formData.gender === 'male'
                      ? 'bg-cyan-500 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => setFormData(prev => ({ ...prev, gender: 'female' }))}
                  className={`py-3 rounded-xl font-medium transition-all ${
                    formData.gender === 'female'
                      ? 'bg-cyan-500 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  
                </button>
              </div>
            </div>

            {/* MBTI */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                MBTI
              </label>
              <input
                type="text"
                name="mbti"
                value={formData.mbti}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                placeholder=": ENFP"
                maxLength={4}
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="text"
                name="school"
                value={formData.school}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                placeholder=" "
                maxLength={50}
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="height"
                value={formData.height}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value="150 ">150 </option>
                <option value="150~155">150~155</option>
                <option value="155~160">155~160</option>
                <option value="160~165">160~165</option>
                <option value="165~170">165~170</option>
                <option value="170~175">170~175</option>
                <option value="175~180">175~180</option>
                <option value="180~185">180~185</option>
                <option value="185 ">185 </option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="bodyType"
                value={formData.bodyType}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="style"
                value={formData.style}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="religion"
                value={formData.religion}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="smoking"
                value={formData.smoking}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select
                name="drinking"
                value={formData.drinking}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="text"
                name="location"
                value={formData.location}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                placeholder=": , "
                maxLength={50}
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <div className="flex gap-2 mb-2">
                <input
                  type="text"
                  value={newInterest}
                  onChange={(e) => setNewInterest(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && (e.preventDefault(), handleAddInterest())}
                  className="flex-1 px-4 py-2 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500"
                  placeholder="     "
                  maxLength={20}
                />
                <button
                  type="button"
                  onClick={handleAddInterest}
                  className="px-4 py-2 bg-cyan-500 text-white rounded-xl hover:bg-cyan-600 transition-all"
                >
                  
                </button>
              </div>
              <div className="flex flex-wrap gap-2">
                {interests.map((interest) => (
                  <span
                    key={interest}
                    className="bg-cyan-100 text-cyan-700 px-3 py-1 rounded-full text-sm flex items-center gap-1"
                  >
                    {interest}
                    <button
                      type="button"
                      onClick={() => handleRemoveInterest(interest)}
                      className="hover:text-red-500 transition-colors"
                    >
                      
                    </button>
                  </span>
                ))}
              </div>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-cyan-500 resize-none"
                placeholder=" "
                rows={4}
                maxLength={200}
              />
              <p className="text-xs text-gray-500 mt-1 text-right">
                {formData.bio.length}/200
              </p>
            </div>

            {/*   */}
            <button
              type="submit"
              disabled={loading || uploading || !formData.name.trim() || !formData.gender}
              className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 text-white py-4 rounded-xl font-medium hover:from-cyan-600 hover:to-blue-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {uploading ? '  ...' : loading ? ' ...' : ' '}
            </button>
          </form>

          {/*   */}
          <div className="mt-6 text-center text-sm text-gray-500">
            <p>: {state.phoneNumber}</p>
          </div>
        </div>
      </div>
    </div>
  );
}
